@import 'variables-functions-mixins';
@import 'website-variables';

/* Downloads
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.downloads {
  overflow-x: scroll;

  ul.tabs {
    border-bottom: 1px solid $gray-2;
    display: flex;
    list-style: none;
    margin: 0;
    overflow-x: scroll;
    padding: 0;

    li {
      border-bottom: 2px solid transparent;
      display: inline-block;
      opacity: 0.5;
      padding: 0;

      &.selected {
        border-bottom: 2px solid $blue-medium;
        opacity: 1.0;
      }


      /* Make the anchor the full width so there is more 'tab' to click on */
      a, a:hover, a:visited, a:link {
        color: $body-font-color;
        display: block;
        padding: 10px 20px;
        width: 100%;

        > span {
          display: none;
        }

        @include media(lg) {
          padding: 10px 60px;

          > span {
            display: inline-block;
          }
        }
      }

      img {
        display: inline-block;
        height: 30px;
        margin-left: 5px;
        margin-right: 10px;
        vertical-align: middle;

        @include media(lg) {
          margin-left: 0;
        }
      }
    }
  }

  .tab-content {
    display: none;
    padding: 40px 0 0 0;

    &.active {
      display: none;

      @include media(lg) {
        display: block;
      }
    }

    .download-command {
      display: flex;
      margin-bottom: 30px;

      > button {
        border-radius: 3px;
        margin-left: 3px;

        --fa-primary-color: #fff;
        --fa-secondary-color: #fff;
        --fa-secondary-opacity: 0.9;

        i {
          font-size: 1.5em;
        }
      }

      code {
        background: $code-background;
        border-radius: 3px;
        color: white;
        flex-grow: 1;
        overflow-x: scroll;
        padding: 14px 15px;
        white-space: nowrap;
      }
    }
  }
}
